@import "../../styles/fonts.css";
@import "../../styles/colors.css";

.controls {
  padding: 1ch;
  border: 1px solid gray;
  background: white;
}

.input-group-field {
  height: 2.5rem;
  margin: 0;
}

.download-form {
  & section {
    margin-top: 2ch;
  }

  & h6 {
    font-weight: bold;
  }

  & .button-group button {
    margin-left: 2px;
    &:first-of-type {
      margin-left: 0;
    }
  }
}

input.invalid-accession,
input.invalid-accession:focus {
  background-color: var(--colors-alert-main);
}
